import React, {useEffect, useState} from 'react'
import './Banner.css';
import axios from '../../axios'
import {API_KEY, imageUrl} from '../../constants/constants'

// Random Number Generator
var rn = require('random-number');
var gen = rn.generator({
    min:  0, 
    max:  19, 
    integer: true
})
const ran = gen()

function Banner() {
    const [movie, setMovie] = useState()
    useEffect(() => {
        axios.get(`trending/all/week?api_key=${API_KEY}&language=en-US`).then((response)=>{
            console.log(response.data.results[ran]);
            setMovie(response.data.results[ran])
        })
    }, [])
    return (
        <div  style={{backgroundImage: `url(${movie ? imageUrl+movie.backdrop_path : "" })`}}   className="banner">
            <div className="content">   
                <h1 className="title">{movie ? movie.title || movie.name || movie.original_name : ""}</h1>
                <div className="banner_buttons">
                    <button className="button"><span>Play</span></button>
                    <button className="button"><span>My List</span></button>
                </div>
                <h1 className="description">{movie ? movie.overview : ""}</h1>
            </div>
        <div className="fade_bottom"></div>
        </div>
    )
}

export default Banner
